
/* Apply a modern font to the entire component */
body {
    font-family: 'Poppins', sans-serif;
    color: #333;
}

/* Container for About Section */
.about {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px 20px;
    background-color: #f4f4f9;
}

/* About Container */
.about-container {
    display: flex;
    width: 100%;
    max-width: 1200px;
    gap: 50px; /* More space between sections for a cleaner look */
}

/* About Content */
.about-content {
    display: flex;
    width: 100%;
}

/* About Text */
.about-text {
    flex: 60%; /* Takes 60% of the width */
    text-align: left;
}

.about-text h2 {
    font-size: 3rem; /* Larger and more prominent heading */
    font-weight: 700; /* Bold font for heading */
    color: #1e90ff; /* Add a pop of color */
    margin-bottom: 30px;
    border-left: 5px solid #1e90ff;
    padding-left: 15px;
}

.about-text p {
    font-size: 1.25rem;
    line-height: 1.8;
    margin-bottom: 25px;
    color: #555;
}

/* Skills Section */
.about-skills {
    flex: 40%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.about-skills h3 {
    font-size: 2.5rem;
    font-weight: 600;
    margin-bottom: 20px;
    text-align: left;
    color: #1e90ff;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 25px;
    align-items: center;
}

/* Skill Cards */
.skill-card {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    text-align: center;
    padding: 25px;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
    cursor: pointer;
}

.skill-card:hover {
    transform: scale(1.08);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skill-card svg {
    margin-bottom: 12px;
    color: #1e90ff;
    font-size: 2rem;
}

.skill-card:nth-child(even) {
    background-color: #eef7ff;
}

.skill-card:nth-child(odd) {
    background-color: #f7fbff;
}

/* Mobile Layout (Max Width 768px) */
@media (max-width: 768px) {
    .about-container {
        flex-direction: column;
        gap: 30px;
    }

    .about-content {
        flex-direction: column;
    }

    .about-text {
        text-align: center;
    }

    .about-text h2 {
        font-size: 2.5rem;
    }

    .about-text p {
        font-size: 1.1rem;
    }

    .about-skills {
        text-align: center;
    }

    .skills-grid {
        grid-template-columns: 1fr 1fr;
    }

    .skill-card {
        font-size: 0.9rem;
    }
}
