/* Footer Container */
.footer {
    background-color: #1c1c1e; /* Dark background */
    color: #ffffff; /* White text */
    padding: 20px 0;
    text-align: center;
    font-family: 'Roboto', sans-serif;
}


/* Social Icons Container */
.footer .social-links {
    display: flex;
    justify-content: center;
    gap: 20px; /* Increased spacing between icons */
    
}

/* Individual Social Icons */
.footer .social-links a {
    color: #ffffff; /* White icons */
    font-size: 1.6rem; /* Very large icon size */
    transition: color 0.3s ease, transform 0.3s ease; /* Smooth hover effect */
}

/* Hover Effect */
.footer .social-links a:hover {
    color: #1e90ff; /* Light blue hover color */
    transform: scale(1.4); /* Increase size further on hover */
}

/* Footer Text */
.footer p {
    /* margin: 20px 0 0; */
    font-size: 1.1rem; /* Slightly larger font for copyright text */
    color: #aaaaaa; /* Subtle gray text */
}

@media (max-width: 768px) {
    .footer p {
        /* margin: 20px 0 0; */
        font-size: 0.9rem; /* Slightly larger font for copyright text */
        color: #aaaaaa; /* Subtle gray text */
    }

    .footer .social-links a {
        color: #ffffff; /* White icons */
        font-size: 1.2rem; /* Very large icon size */
        transition: color 0.3s ease, transform 0.3s ease; /* Smooth hover effect */
    }
    
}