@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

body {
  font-family: 'Poppins', sans-serif;
}

.header {
  font-family: 'Poppins', sans-serif;
}

.logo a,
.nav-links a {
  font-family: 'Poppins', sans-serif;
}

/* General Header Styling */
.header {
  position: sticky;
  top: 20px;
  width: calc(100% - 20%);
  margin: 0 auto;
  background-color: #1e1e1e;
  color: #ffffff;
  border-radius: 30px;
  z-index: 1000;
  padding: 1px 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 10px;
  margin-right: 10px;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #ffffff;
}

.logo a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
}

.logo a:hover {
  color: #61dafb;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  transition: transform 0.3s ease;
}

.nav-links a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
}

.nav-links a.active {
  color: #61dafb;
  text-shadow: 0 0 10px #61dafb;
  font-weight: bold;
}

.nav-links a:hover {
  color: #61dafb;
}

/* Hamburger Icon */
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  gap: 5px;
}

.hamburger .line {
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  transition: all 0.3s ease;
}

.hamburger.open .line:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.hamburger.open .line:nth-child(2) {
  opacity: 0;
}

.hamburger.open .line:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}

.nav-links {
  flex-direction: column;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 40%;
  background-color: #1e1e1e;
  padding: 40px 20px;
  transform: translateX(100%);
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
  z-index: 1001;
}

.nav-links.active {
  display: flex;
  transform: translateX(0);
}

.nav-links li {
  margin: 10px 0;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  transition: all 0.3s ease;
}

.overlay.show {
  opacity: 1;
  visibility: visible;
}

/* Mobile View */
@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .nav-links {
    display: flex;
  }

  .header {
    position: sticky;
    top: 20px;
    width: calc(100% - 20%);
    margin: 0 auto;
    background-color: #1e1e1e;
    color: #ffffff;
    border-radius: 30px;
    z-index: 1000;
    padding: 10px 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
}

/* Tablet and Laptop View */
@media (min-width: 769px) {
  .nav-links {
    position: static;
    flex-direction: row;
    transform: translateX(0);
    background: none;
    box-shadow: none;
    padding: 0;
    width: auto;
    display: flex;
  }

  .overlay {
    display: none;
  }

  .hamburger {
    display: none;
  }
}
.nav-links a.active,
.logo a.active {
  color: #61dafb;
  font-weight: bold;
}
