/* General Projects Section Styling */
.projects {
    padding: 50px 20px;
    background: linear-gradient(120deg, #f5f7fa, #c3cfe2); /* Soft gradient with light tones */
    color: #333; /* Dark text for readability */
    font-family: 'Roboto', sans-serif;
}

.projects h2 {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 30px;
    color: #333;
}

/* Project Grid Layout */
.project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    justify-items: center;
}

/* Project Card Styling */
.project-card {
    position: relative;
    width: 100%;
    height: 290px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 30px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Hover Effect */
.project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

/* Project Image Container */
.project-image-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Adjust Image to fit within the project card */
.project-image {
    width: 100%;  /* Ensure image width is 100% of the container */
    height: 100%; /* Ensure image height is 100% of the container */
    object-fit: contain; /* Make sure the image fits within the container */
}

/* Project Content Styling */
.project-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    opacity: 0;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 0.3s ease;
}

/* Show content on hover or toggle */
.project-card.active .project-content {
    opacity: 1;
}

/* Tech Stack Icons */
.project-content .tech-icon {
    margin: 0 10px;
    font-size: 1.5rem;
}

/* GitHub Icon */
.github-icon {
    font-size: 2rem;
    color: #1e90ff;
    transition: color 0.3s ease;
}

.github-icon:hover {
    color: #4169e1;
}

/* Toggle Button Styling */
.toggle-details-btn {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    background: transparent;
    border: none;
    color: white;
    font-size: 2rem;
    cursor: pointer;
    transition: color 0.3s ease;
}

.toggle-details-btn:hover {
    color: #4169e1;
}

.projects h2 {
    font-size: 2.5rem; /* Increase the size for prominence */
    text-align: center;
    margin-bottom: 30px;
    color: #333;
    font-family: 'Poppins', sans-serif; /* Stylish font for headings */
    text-transform: uppercase; /* Convert to uppercase for emphasis */
    letter-spacing: 2px; /* Add spacing between letters */
    position: relative; /* To position the decorative elements */
    display: inline-block;
}

.projects h2::before, 
.projects h2::after {
    content: ''; /* Decorative lines */
    position: absolute;
    width: 50px;
    height: 2px;
    background-color: #1e90ff; /* Add a color accent */
    top: 50%; /* Align with the middle of the text */
    transform: translateY(-50%);
}

.projects h2::before {
    left: -60px; /* Position the line to the left of the heading */
}

.projects h2::after {
    right: -60px; /* Position the line to the right of the heading */
}

