@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&family=Poppins:wght@700&display=swap');

.home {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(135deg, #1e90ff, #87cefa);
    color: #fff;
    padding: 0 20px;
    font-family: 'Open Sans', sans-serif;
}

/* Home Content: Split Layout */
.home-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
}

/* Profile Photo */
.profile-photo {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-photo img {
    width: 400px;
    height: 400px;
    border-radius: 50%;
    border: 5px solid #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    object-fit: cover;
}

/* Text Content */
.text-content {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    padding-left: 20px;
    text-align: left;
}

.text-content h1 {
    font-family: 'Poppins', sans-serif;
    font-size: 3.6rem;
    font-weight: 700;
    color: #ffffff;
}

.text-content h1 .highlight {
    color: #ffd700;
}

.text-content p {
    font-size: 1.6rem;
    font-weight: 600;
    color: #f0f8ff;
}

/* CTA Buttons */
.cta-buttons {
    display: flex;
    gap: 15px;
    justify-content: flex-start;
}

.btn {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 20px;
    background-color: #fff;
    color: #1e90ff;
    text-decoration: none;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    transition: transform 0.2s, background-color 0.2s, color 0.2s;
}

.btn:hover {
    background-color: #1e90ff;
    color: #fff;
    transform: scale(1.05);
}

/* Mobile View */
@media (max-width: 767px) {
    .home-content {
        flex-direction: column;
        text-align: center;
    }

    .profile-photo {
        margin-bottom: 20px;
    }

    .text-content {
        padding-left: 0;
    }

    .profile-photo img {
        width: 200px;
        height: 200px;
    }

    .text-content h1 {
        font-size: 2.5rem;
        text-align: center;
    }

    .text-content p {
        font-size: 1.2rem;
        text-align: center;
    }

    .cta-buttons {
        justify-content: center;
        margin-top: 20px;
    }
}

/* Laptop View */
@media (min-width: 768px) and (max-width: 1200px) {
    .profile-photo img {
        width: 300px;
        height: 300px;
    }

    .text-content h1 {
        font-size: 3rem;
    }

    .text-content p {
        font-size: 1.5rem;
    }

    .cta-buttons {
        justify-content: flex-start;
    }
}
